import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarCheck, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import './Hero.css';
import { isMobile } from 'react-device-detect';
import OurServices from './OurServices';

function Hero() {
	const navigate = useNavigate();
	const [goUp, setGoUp] = useState(false);

	const scrollToTop = () => {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	};



	useEffect(() => {
		const onPageScroll = () => {
			if (window.scrollY > 600) {
				setGoUp(true);
			} else {
				setGoUp(false);
			}
		};
		window.addEventListener('scroll', onPageScroll);

		return () => {
			window.removeEventListener('scroll', onPageScroll);
		};
	}, []);

	return (
		<>
			<div className="hero-main" style={{ height: isMobile ? '70vh' : '90vh', padding: isMobile ? '120px 0 0 0' : '100px 0 0 0' }}>
				<div className="hero-section-main" style={{ height: isMobile ? '' : '70vh', alignContent: 'center', alignItems: 'center', justifyItems: 'center', display: 'flex', padding: '50px 50px 0 50px', background: isMobile ? '' : '' }}>
					<div className="text-section" style={{ padding: isMobile ? '0 20px' : '0 80px', display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
						<p className="text-headline" style={{ color: 'black', fontWeight: isMobile ? '' : '500', letterSpacing: isMobile ? '' : '2px' }}>
							{`Work with us today`}
						</p>
						<h2 className="text-title" style={{ fontWeight: isMobile ? 'bold' : '500', fontSize: isMobile ? '30px' : '50px', color: 'black', letterSpacing: isMobile ? '0.5px' : '3px' }}>
							{`Impact through simple solutions`}
						</h2>
						<p className="text-descritpion" style={{ color: 'black', fontWeight: isMobile ? '' : '500', textAlign: 'right' }}>
							{`We listen, understand, and deliver simple effective solutions. With clear requirements and strong collaboration, no solution is too complex. Let our technical expertise transform your business processes.`}
						</p>
					</div>
					{!isMobile && <div className="text-section" style={{ padding: '0px 50px 0px 50px' }}>
						<div className="ad-section-container" />
					</div>}
				</div>
			</div>
			<div className="hero-section" style={{ margin: '0vh 0', display: isMobile ? 'none' : 'flex', flexDirection: 'column' }}>
				<div className="text-title" style={{ fontSize: '40px', fontWeight: 'normal', padding: '10px' }}>
					{`We offer these services to turn partner requirements into reality.`}
				</div>
				<OurServices />
				<button className="text-appointment-btn" type="button" onClick={() => { }} style={{}}>
					<a style={{ display: 'flex' }} href={`mailto:support@flowhero.app?subject=FlowHeroApps%20Hosted%20Inquiry&body=I would like to learn more from FlowHeroApps`}>

						<p>
							<FontAwesomeIcon icon={faCalendarCheck} />
							{' Contact Us To Learn More.'}
						</p>
					</a>
				</button>


			</div>
			<div onClick={scrollToTop} className={`scroll-up ${goUp ? 'show-scroll' : ''}`}>
				<FontAwesomeIcon icon={faAngleUp} />
			</div>
		</>
	);
}
export default Hero;